import React from 'react';

import {useDispatch, useSelector} from 'react-redux'
import { getPokemonsAction, nextPokemonAction } from '../redux/pokeDucks.js'

const Pokemons = () => {

    const dispatch = useDispatch();

    const pokemons = useSelector(store => store.pokemons.array);
    console.log(pokemons);

    return (
        <div>
            List of Pokemons
            <button onClick={() => dispatch(getPokemonsAction())}>Get Pokemons</button>
            <button onClick={() => dispatch(nextPokemonAction())}>Next Page</button>
            <ul>
                {
                    pokemons.map(item => (
                        <li key={item.name}>{item.name}</li>
                    ))
                }
            </ul>
        </div>
    );
}

export default Pokemons;